import React from 'react'
import Icon1 from '../../images/img13.svg'
import Icon2 from '../../images/img14.svg'
import Icon3 from '../../images/img15.svg'
import { ServicesContainer, ServicesH1, ServicesWrapper, ServicesCard, ServicesIcon, ServicesH2, ServicesP } from './ServicesElements'

const Services = () => {
    return (
        <ServicesContainer id="services">
            <ServicesH1>Services</ServicesH1>
            <ServicesWrapper>
                <ServicesCard>
                    <ServicesIcon src={Icon1}/>
                    <ServicesH2>Vaullti-PRO</ServicesH2>
                    <ServicesP>Enhanced marketplace with more postings, more images, and smarter analytics. </ServicesP>
                </ServicesCard>
                <ServicesCard>
                    <ServicesIcon src={Icon2}/>
                    <ServicesH2>Custodian</ServicesH2>
                    <ServicesP>Active scan websites and alert customers when their property is being distributed illegally.  </ServicesP>
                </ServicesCard>
                <ServicesCard>
                    <ServicesIcon src={Icon3}/>
                    <ServicesH2>Vaullti-ML</ServicesH2>
                    <ServicesP>Machine learning models predicting material usages, regional growth, company growth, and time optimization. </ServicesP>
                </ServicesCard>
            </ServicesWrapper>
            
        </ServicesContainer>
    )
}

export default Services
